import React, { Component } from 'react';
import flag from './flag.gif';
import './App.css';
import {Greek} from './greek'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      norwegian: '',
      greek: '',
      transcriber: new Greek(''),
      language: 'greek',
    }
  }

  changeGreek = (event) => {
    this.setState({greek: event.target.value})
    this.state.transcriber.setString(event.target.value)
  }

  render() {
    return (
  <div className="App">
    <div className="App-header">
      <img src={flag} className="App-logo" alt="logo" />
      <h2>Transliterasjon av nygresk!</h2>
    </div>

    <div className="Greek-form">
      <form>
        <input type="text"
        value={this.state.greek}
        onChange={this.changeGreek}
        placeholder="Dette er jo helt gresk!"/>
      </form>
      <p>{this.state.transcriber.norwegian()}</p>
    </div>
    </div>
    );
  }
}

export default App;



